<template>
  <div class="ev-data">
    <md-layout md-align="center">
      <md-table-card v-for="(row, rowIndex) in records" :key="rowIndex" style="margin: 10px; width: 70%">
        <md-toolbar>
          <h1 class="md-title">{{ rowIndex }}</h1>
        </md-toolbar>
        <md-table>
            <md-table-header>
              <md-table-row>
                <md-table-head style="text-align: center;">Month</md-table-head>
                <md-table-head style="text-align: center;">Number</md-table-head>
              </md-table-row>
            </md-table-header>

            <md-table-body>
                <md-table-row v-for="(col, colIndex) in row" :key="colIndex">
                  <md-table-cell>{{ colIndex+1 }}</md-table-cell>
                <md-table-cell>{{ col }}</md-table-cell>
                </md-table-row>
            </md-table-body>
        </md-table>
        </md-table-card>
    </md-layout>
  </div>
</template>

<script>
import evData from '@/assets/ev.json'

export default {
  name: 'ev-data',
  data: () => ({
    records: evData
  })
}
</script>



