<template>
  <div class="tesla-data">
    <md-layout md-align="center">
      <md-table-card v-for="(row, rowIndex) in records" :key="rowIndex" style="margin: 10px; width: 70%">
        <md-toolbar>
          <h1 class="md-title">{{ rowIndex }}</h1>
        </md-toolbar>
        <md-table>
            <md-table-header>
              <md-table-row>
                <md-table-head style="text-align: center;">Month</md-table-head>
                <md-table-head style="text-align: center;">Saloon</md-table-head>
                <md-table-head style="text-align: center;">Station Waggon</md-table-head>
              </md-table-row>
            </md-table-header>

            <md-table-body>
                <md-table-row v-for="(col, colIndex) in row" :key="colIndex">
                  <md-table-cell v-if="rowIndex==2016">{{ colIndex+5 }}</md-table-cell>
                  <md-table-cell v-if="rowIndex!=2016">{{ colIndex+1 }}</md-table-cell>
                  <md-table-cell v-for="(ele, eleIndex) in col" :key="eleIndex">{{ ele }}</md-table-cell>
                </md-table-row>
            </md-table-body>
        </md-table>
        </md-table-card>
    </md-layout>
  </div>
</template>

<script>
import teslaData from '@/assets/tesla.json'

export default {
  name: 'tesla-data',
  data: () => ({
    records: teslaData
  })
}
</script>



