<template>
    <div class="clp">
        <md-layout md-align="center">
            <md-table>
                <md-table-header>
                    <md-table-row>
                    <md-table-head style="text-align: center; width: 100px">Location</md-table-head>
                    <md-table-head style="text-align: center; width: 100px">Available Chargers</md-table-head>
                    <md-table-head style="text-align: center; width: 100px">Quick Charger</md-table-head>
                    <md-table-head style="text-align: center; width: 100px">Semi Quick Charger</md-table-head>
                    <md-table-head style="text-align: center; width: 100px">Last Update</md-table-head>
                    </md-table-row>
                </md-table-header>

                <md-table-body>
                    <md-table-row v-for="(row, rowIndex) in records.geolocationResult" :key="rowIndex">
                    <md-table-cell>{{row.Title}}</md-table-cell>
                    <md-table-cell>{{row.TotalNumberOfAvailableCharger}} out of {{row.TotalNumberOfCharger}}</md-table-cell>
                    <md-table-cell>{{row.TotalNumberOfAvailableQuickCharger}} out of {{row.TotalNumberOfQuickCharger}}</md-table-cell>
                    <md-table-cell>{{row.TotalNumberOfAvailableSemiQuickCharger}} out of {{row.TotalNumberOfSemiQuickCharger}}</md-table-cell>
                    <md-table-cell>{{row.LastUpdate}}</md-table-cell>
                    </md-table-row>
                </md-table-body>
            </md-table>
        </md-layout>
    </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'clp',
  data: () => ({
    records: []
  }),
  created () {
    axios.get('/clpdata/api/CLPEVCharger/GetLevel3ListByFilter?provider=CLP&chargerType=&districtItemId=&status=&lang=zh')
    .then(response => {
      // JSON responses are automatically parsed.
      this.records = response.data
    })
    .catch(e => {
      this.errors.push(e)
    })
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

</style>



