import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

// Common
import Common from '@/components/common/Common'
import Bottom from '@/components/common/Bottom'
Vue.component('common', Common)
Vue.component('bottom', Bottom)

// Config
import Config from '@/components/config/Config'
import ModelS from '@/components/config/ModelS'
import ModelX from '@/components/config/ModelX'
import Model3 from '@/components/config/Model3'
Vue.component('config', Config)
Vue.component('models', ModelS)
Vue.component('modelx', ModelX)
Vue.component('model3', Model3)

// Charger
import Chargers from '@/components/charger/Chargers'
import SuperCharger from '@/components/charger/Supercharger'
import CLP from '@/components/charger/CLP'
Vue.component('chargers', Chargers)
Vue.component('supercharger', SuperCharger)
Vue.component('clp', CLP)

// Statistic
import Statistic from '@/components/statistic/Statistic'
import Tesla from '@/components/statistic/Tesla'
import EV from '@/components/statistic/EV'
Vue.component('statistic', Statistic)
Vue.component('tesla-data', Tesla)
Vue.component('ev-data', EV)

import News from '@/components/News'
import Firmware from '@/components/Firmware'
import BugList from '@/components/BugList'
import Accessory from '@/components/Accessory'

Vue.component('news', News)
Vue.component('firmware', Firmware)
Vue.component('buglist', BugList)
Vue.component('accessory', Accessory)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'app',
      component: Config
    },
    {
      path: '/Config',
      name: 'config',
      component: Config
    },
    {
      path: '/News',
      name: 'news',
      component: News
    },
    {
      path: '/Chargers',
      name: 'chargers',
      component: Chargers
    },
    {
      path: '/Firmware',
      name: 'firmware',
      component: Firmware
    },
    {
      path: '/BugList',
      name: 'buglist',
      component: BugList
    },
    {
      path: '/Statistic',
      name: 'statistic',
      component: Statistic
    },
    {
      path: '/Accessory',
      name: 'accessory',
      component: Accessory
    }
  ]
})



