<template>
  <div class="firmware">
    <md-layout md-align="center">
      <md-table-card v-for="(row, rowIndex) in records" :key="rowIndex" style="margin: 10px; width: 70%">
            <md-toolbar>
                <h1 class="md-title">{{ rowIndex }}</h1>
            </md-toolbar>
        <md-table>
            <md-table-header>
                <md-table-row>
                <md-table-head style="text-align: center;">Build</md-table-head>
                <md-table-head style="text-align: center;">Model</md-table-head>
                <md-table-head style="text-align: center;">Autopilot</md-table-head>
                <md-table-head style="text-align: center;">Date</md-table-head>
                </md-table-row>
            </md-table-header>

            <md-table-body>
                <md-table-row v-for="(col, colIndex) in row" :key="colIndex">
                <md-table-cell v-for="(ele, eleIndex) in col" :key="eleIndex">{{ ele }}</md-table-cell>
                </md-table-row>
            </md-table-body>
        </md-table>
        </md-table-card>
    </md-layout>
    
  </div>
</template>

<script>
import firmwareData from '@/assets/firmware.json'

export default {
  name: 'firmware',
  data: () => ({
    records: firmwareData
  })
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
</style>



